body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  color:blue;
}

.Intro {
  background-image: url(/static/media/healthcare2.06e7be1f.gif);
  background-blend-mode: lighten;
  background-size: cover;
  background-repeat: no-repeat;
}



.ListContent {
  font-style: italic;
  text-align: left;
}

.MainContent {
  background-color: #6ebdee;
  background-image: url(/static/media/IntroPicture.24f0cb55.jpg);
  background-blend-mode: hard-light;
}

.Content {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-size: large;
}

.animate-charcter
{
  text-transform: uppercase;
  background-image: linear-gradient(
    -225deg,
    #f4e5e5 0%,
    #6ebdee 29%,
    #0d6e7d 67%,
    #0755af 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  -webkit-text-emphasis-color: transparent;
          text-emphasis-color: transparent;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  -webkit-animation: textclip 2s linear infinite;
          animation: textclip 2s linear infinite;
  display: inline-block;
  align-self: center;
  font-size: 60px;
  margin-top: 0px;
  margin-bottom: 185px;
}

@-webkit-keyframes textclip {
  to {
    background-position: 200% center;
  }
}

@keyframes textclip {
  to {
    background-position: 200% center;
  }
}

h1 {
  color: #000000;
  font-style: italic;
  font-size: xx-large;
  font-weight: bold;
}

ul {
  margin-bottom: 2%;

}

li {
  font-weight: bold;
  margin-bottom: 1%;
}





